* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul {
  list-style: none;
}

body {
  background-color: #121214;
}

body * {
  font-family: 'Inter', sans-serif;
  line-height: 160%;
}

header {
  text-align: center;
  margin-top: 32px;
  height: calc(120px + 32px);
}

header div {
  width: 150px;
  margin: auto;
  background-image: linear-gradient(90deg, #9572FC 0%, #43E7AD 50%, #E2D45C 100%);
  
  border-radius: 50%;
  display: flex; /* Para alihnar a única imagem ao centro. */
  padding: 4px;

  transition: transform .3s;

}

header div:hover {
  transform: scale(1.1);
}

header img {
  width: 100%;
  border-radius: 50%;
}

main {
  max-width: 580px;
  width: 90%;
  margin: 32px auto; /* (32px, top and bottom) (auto, left and right) */
  
  display: grid;
  gap: 24px;
}

section {
  color: white;
  background-image: linear-gradient(90deg, #9572FC 0%, #43E7AD 50%, #E2D45C 100%);  
  padding-top: 4px;
  border-radius: 10px;
}

section div {
  background-color: #2A2634;
  padding: 32px;
  padding-top: 24px;
  border-radius: 8px;
}

section h2 {
  /* font-weight: 900; Linha de código não precisa, sem alterações na página, navegador atualizou. */
  /* font-size: 24px; Linha de código não precisa. sem alterações na página, navegador atualizou. */
  letter-spacing: -0.47px;
}

section p {
  /* font-weight: 400; Linha de código não precisa, sem alterações na página, navegador atualizou. */
  /* font-size: 16px; Linha de código não precisa, sem alterações na página, navegador atualizou. */
  letter-spacing: -0.18px;
  color: #a1a1aa;
}

section ul {
  display: flex;
  gap: 16px;
  margin-top: 24px;    
}

section ul li {
  transition: transform .3s;
}

section ul li:hover {
  transform: scale(1.1);
}

.games-list img {
  width: 90%;
  border-radius: 8px;
}

.channel-list img {
  border-radius: 50%;
  width: 70px;
  border: 1px solid #443E54;
}

/* ANIMATION */

@keyframes fromtop {
  from {
      opacity: 0;
      transform: translateY(-30px);
  }
  to {
      opacity: 1;
      transform: translateY(0px);
  }
}

header div {
  animation: fromtop .7s .2s backwards;
}

@keyframes frombottom {
  from {
      opacity: 0;
      transform: translateY(30px);
  }
  to {
      opacity: 1;
      transform: translateY(0px);
  }
}

main section {
  animation: frombottom .7s backwards;
  animation-delay: 0;
}

main section:nth-child(1) {
  animation-delay: 0s;
}   

main section:nth-child(2) {
  animation-delay: .4s;
}   

main section:nth-child(3) {
  animation-delay: .8s;
}